import{row} from "../utils";

export class SelectorToDelete{
    constructor(selector,length) {
        this.selector=selector
        this.length=length
    }
    getVariants(size){
        let html=`<option>1</option>`
        for(let i=2;i<=size;i++){
            html=`${html}
                    <option>${i}</option>`
        }
        return html
    }
    toHTML(){
        return row(`<label for="${this.selector}">–í—ã–±–µ—Ä–∏—Ç–µ –Ω–æ–º–µ—Ä —Å—Ç—Ä–æ–∫–∏ –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è:</label><select id="${this.selector}">${this.getVariants(this.length)}</select>`)
    }
}
export class SelectorToRedact{
    constructor(tables,id) {
        this.id=id
        this.tables=tables
    }
    getVariants(content){
        let html=``
        for(let i=0;i<content.length;i++){
            html=`${html}
                    <option>${content[i]}</option>`
        }
        return html
    }
    toHTML(){
        return row(`<label for="${this.id}">–í—ã–±–µ—Ä–∏—Ç–µ —Å—Ç—Ä–æ–∫—É –¥–ª—è —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è:</label><select id="${this.id}">${this.getVariants(this.tables)}</select>`)
    }
}
