import{Delete,Add} from "./editor";

export function DeleteEvent(key='',selector_id=''){
    let value=document.getElementById(selector_id).value
    Delete(key,value)
}
export function UpdateEvent(key,input_id,selector_id){
    let value=document.getElementById(selector_id).value
    let input=document.getElementById(input_id).value
    console.log(input)
    let row=Array(input.split(','))
    switch(value){
        case 'Software':
            Add(row,key[1])
        case 'Computers':
            Add(row,key[0])
        case 'Office staff':
            Add(row,key[2])
    }
}